var searchParams = new URLSearchParams(document.currentScript.src.split('?')[1]);
var mbExtId = searchParams.get('extId');

console.debug("INS: Instart scanner loaded into " + window.location.href + " by " + mbExtId);
(function() {
    function checkForInstartLogic(label) {
        if (document.currentScript && document.currentScript.textContent) {
            let scriptText = document.currentScript.textContent;
            let keywords = ["Instart", "instart", "INSTART", "I10C", "I11C", "I12C", "IXC_353_1119062411120848"];
            for (let keyword of keywords) {
                if (scriptText.includes(keyword)) {
                    throw new Error("Instart " + label + " Logic");
                }
            }
        }
    }

    function onHook(object, property) {
        console.debug("INS: " + property + " hooked for " + window.location.href);
        let value = object[property];
        Object.defineProperty(object, property, {
            get: function() {
                checkForInstartLogic(property); return value;
            },
            set: function(newValue) {
                checkForInstartLogic(property); value = newValue;
            }
        });
    }

    function hook(_object, property) {
        console.debug("INS: Sending an is-whitelisted request about " + property);
        console.debug("INS: Extension ID " + mbExtId);
        // fallback to window.postMessage for Safari and Firefox
        const params = {
            href: window.location.href,
            prop: property,
        };

        console.debug("INS:checking isInstartWhitelisted for ", {params});
        window.postMessage({
            type: 'isInstartWhitelisted',
            parameters: JSON.stringify(params),
        }, `${window.location.protocol}//${window.location.host}`);
    }

    hook( window, "INSTART" );
    hook( window, "INSTART_TARGET_NAME" );
    hook( window, "I10C" );
    hook( window, "I11C" );
    hook( window, "I12C" );
    hook( window, "IXC_353_1119062411120848" );

    let oldSetTimeout = window.setTimeout;
    window.setTimeout = function() {
        checkForInstartLogic("Timeout");
        return oldSetTimeout.apply(window, arguments);
    };

    let oldSetInterval = window.setInterval;
    window.setInterval = function() {
        checkForInstartLogic("Interval");
        return oldSetInterval.apply( window, arguments );
    };

    window.addEventListener('message', (event) => {
        if (!event.origin) {
            return;
        }
        if (event.origin !== window.location.origin) {
            return;
        }
        const originUrl = new URL(event.origin);
        const eventHost = originUrl.host;
        if (event.data.type === 'isInstartWhitelistedResponse' && eventHost === window.location.host) {
            console.debug("INS: got isInstartWhitelistedResponse for ", {event});
            const params = JSON.parse(event.data.parameters);
            if (params.isWhitelisted === false) {
                onHook(window, params.property);
            }
        }
    });
})();
